            osp_l4: 'Use primitives to manage either binary or text files',
            osp_date: 'June 23rd, 2019',
            osp_details: 'About the project',
            osp_d1: "Cryptography is a group of strategies that allows us to transform the information with the goal of hiding it to non authorized third parties. The most used algorithms rely on substitution skills to modify the original text and obtain the encoded text. A very easy and old algorithm used is the caesar's code, which uses a substitution that works in this way: Each letter from the original text will be translated for the letter that is two positions forward in the alphabet. The last letters from the alphabet (Y and Z) will be replaced for A and B respectively, which means that the substitution strategy is circular. For instance, we will encode the following text",
            osp_d1w: 'slo te lo dir maana al medioda en la biblioteca',
            osp_d2: 'will be replaced for',
            osp_d2w: 'uqnq vg nq fktg cpcoc cn gfkqfkc go nc ekenkqvgec',
            osp_d3: 'Another very used algorithm is the Murcielago code, which consists in the following: Initially, the word murcielago has the characteristic of having all the vowels and have a length of 10 characters. In this way, it is proposed a substitution from letter to number under the following pattern',
            osp_t: 'Letter',
            osp_c: 'Conversion',
            osp_d4: 'This repository has a program written in C of the crypting/decrypting program, there is a version of 2 children processes and n children processes, the root will apply one crypting/decrypting method and each children will apply the other, the blank spaces will be deleted. The decyphering program is exactly the opposite, given the cyphered text in a text file, it will obtain the clear text without blank spaces or tabs. That means that the children processes will execute the reverser Murcielago code. The output files will be received by the superior level and will apply the caesar code in reverse.',
            osp_usage: 'Uso',
            osp_nchildren: 'NumberOfChildren',
            osp_inputfile: 'InputFileName',
            osp_outputfile: 'OutputFileName',
            osp_usagen: 'for the version of n children',
            osp_usagetwo: 'for the version of 2 children',

            /* OS Fork and Thread */
            osft_tit: 'Concurrent programming',
            osft_date: 'June 19th, 2019',
            osft_d4: "This repository contains three versions of the encoding and decoding program: A sequential and two concurrent versions: One version uses processes and the other versions uses threads. For the concurrent versions a tree of processes/threads was used, it was composed by three levels: root, medium level and leaves and it works the following way: To encode the program will receive the clear text in a file and it will be encoded using caesar and Murcielago codes deleting the blank spaces. The processes/threads that will act as the leaves of the tree will apply caesar's code to their part of the text. The medium processes/threads will recover the text from the files created by their children, will concatenate their text and apply the murcielago code to their part of the text. The root process/thread will receive the encoded text part from all their children and concatenate the text that comes from the files made by their children to finally obtain the encoded text in an output file", 
            osft_d42: "The decrypting program is exactly the opposite, as given the encoded text in a text file, the program will obtain the clean text without blank spaces. Which means that all the threads/processes that act like leaves will execute the reversed Murcielago code. The generated files will be recovered by the medium processes and will apply the reverse caesar's code. Finally, the created files will be recovered by the root process/thread which will just concatenate the files from the medium level and obtain the final text file with the clean word (without spcaes, which were deleted when the original file was encrypted).",
