            eq_tit: 'Earthquake',
            eq_desc: 'Simple earthquake simulation, each of the given 1..9 regions contains 2 matrixes, one for the possible economic loss and another for the possible number of deaths after the earthquake, the program will calculate the total cost/# of victims and other relevant data.',
            eq_date: 'January 8th, 2018',
            eq_req: 'Requeriments',
            eq_req1: "The territory given will be a record with 1 to 9 regions. Each geographic region is represented by a matrix (M x N) where each item inside represents a geographic grid. Each region doesn't have more than 900 grids (M = 30 and N = 30 as maximum)",
            eq_req2: 'Each earthquake has an epicenter, which is the spot where it is iniciated, the epicenter will be represented by a geographic grid [i, j].',
            eq_req3: "Each geographic region has 2 matrixes M x N, one represents COST. each grid represents economic losses to consider if an earthquake attacked given zone. There's as well a matrix that represents DEATHS, where the estimation of human losses will be saved. This data is rounded up and expressed as integer numbers.",
            eq_req4: 'The expansive wave of the earthquake is circular and depends of its degree, thus an earthquake of 1 degree will affect all the grids nearby the epicenter, an earthquake of 2 degrees will affect the grids nearby the epicenter and the grids nearby the ones affected by an earthquake of 1 degree and so on. For example (Cyan represents epicenter, yellow zone affected):',
            eq_f: 'First degree earthquake',
            eq_s: 'Second degree earthquake',
            eq_r: 'Real example with a second degree earthquake',
            eq_total: 'The total COST/DEATHS will be',
            eq_one: 'Upload and modify values of COST and DEATHS',
            eq_oned: 'The program must be capable of loading or modifying the different costs and human losses that belong to a specific geographic region, either manually or using an input file. Aditionally, the program must be capable of loading the values of COST and DEATHS randomly.',
            eq_two: 'Estimate of the consequences of an earthquake',
            eq_twod: 'Given a geographic region, the user will input the coordinates [i,j] of the epicenter and the intensity in degrees of the earthquake. The program must give as a result the economic and human consequences of it.',
            eq_three: 'Estimate of the maximum and minimum risk zone',
            eq_threed: 'From the information a region that will be specified by the user, the program will ask for the user the degrees of the earthquake and it will be calculated the epicenter where it would have the greatest and the smallest human losses.',
            eq_four: 'Estimate of the average cost',
            eq_fourd: 'Given a region and the intensity of an earthquake the program must calculate, considering all the possible epicenters of the region the average cost of an earthquake. The average cost will calculated in this way: The sum of all the economic losses in each grid / Number of grids.',
            eq_five: 'Check a geographic region',
            eq_fived: 'The program must show in screen, in a legible way all the data loaded in a specific geographic region.',


