            snakes_date: '17 de Enero de 2020',
            snakes_ptr: 'Reglas del juego',
            snakes_pr: 'Los jugadores comienzan con una ficha y se turnan para lanzar un dado que les indicar la cantidad de casillas que deben avanzar. Las fichas se mueven segn la numeracin del tablero, en sentido ascendente. Si al finalizar un movimiento un jugador cae en una casilla donde comienza una escalera, sube por ella hasta la casilla donde sta termina. Si, por el contrario, cae en una en donde comienza la cabeza de una serpiente, desciende por sta hasta la casilla donde finaliza su cola.',
            snakes_pr2: 'Si un jugador obtiene un 6 podr mover y tirar nuevamente el dado. Si un jugador obtiene tres 6 consecutivos, deber regresar a la casilla inicial y no podr mover su ficha hasta obtener nuevamente un 6. El jugador que logra llegar a la casilla final es el ganador. En una de las modalidades se debe llegar a la casilla final con el puntaje justo. ',
            snakes_mt: 'Modificaciones generales del juego',
            snakes_mp: 'La siguiente modificacin es incorporada al juego: Si en alguna jugada el equipo jugador cae en una de las 3 casillas especiales que se sealan, el juego invertir su sentido. Cabe destacar que por la topologa y la forma en que se ha configurado el cable de comunicaciones, el juego, normalmente seguir siempre el mismo sentido es decir del jugador 1 al 4 sin embargo al caer el jugador en una de esas casillas especiales se deber tomar en cuenta que a pesar que fsicamente el sentido del juego se mantiene, lgicamente se invertir es decir, el sentido ser del jugador 4 al 1.',
