            /* Reading Club */
            reading_desc: 'Aplicacin web que utiliza una base de datos relacional transaccional',
            reading_goals: 'El sistema contempla lo siguiente',
            reading_l1: 'Administracin de clubes: Mantenimiento de datos bsicos (incluye asociaciones con otros clubes); control de grupos de lectura; mantenimiento de miembros (incluye control de pagos) y mantenimiento de libros',
            reading_l2: 'Administracin de Reuniones: Mantenimiento de calendario mensual de reuniones; elecciones de libros y asignacin de moderadores; control de asistencia y cierre de discusiones',
            reading_l3: 'Planificacin y Ejecucin de Obras Actuadas: Mantenimiento de obras (incluye elenco); mantenimiento de calendario de presentaciones; mantenimiento de valoraciones de actores y obras.',
            reading_date: '16 de Enero de 2020',
            reading_members: 'Lectores',
            reading_m1: 'La cantidad idnea de lectores participantes en un club de lectura oscila entre 5 y 15 personas por grupo. Los lectores de un club pueden ser adultos (desde los 18 aos), nios (entre 9 y 12 aos) y jvenes (desde los 13 a los 17 aos). No se mezclan los grupos aunque un mismo club puede agrupar los tres tipos',
            reading_g1: 'Grupo de adultos',
            reading_g1d: 'Pueden empezar a funcionar cuando haya 10 personas dispuestas a arrancar. Cuando un grupo sobrepasa los 15 integrantes debe dividirse en dos. As pueden incorporarse nuevas personas en cada uno de ellos hasta que se alcance de nuevo la cantidad aludida y sea necesaria una nueva divisin.',
            reading_g2: 'Grupos de nios y jovenes',
            reading_g2d: 'Los grupos pueden empezar a funcionar cuando haya 5 lectores dispuestos a ello, y no conviene que exceda de 10. Los integrantes de clubes independientes aportan anualmente $100,00 por membresa. A medida que aumenten los integrantes se deben planificar ms reuniones y se asignan los grupos a su reunin correspondiente (cada reunin debe tener un moderador, quin facilita las discusiones y comentarios). Los moderadores son tambin miembros del club, pertenecientes al grupo de la reunin  en el caso de los grupos de nios el moderador debe ser un integrante de algn grupo de adultos. Se mantiene el mismo moderador para todas las reuniones relacionadas a un mismo libro.',
            reading_g2d2: 'De cada integrante de un club se debe tener todos sus datos bsicos necesarios (nombre completo, nacionalidad, gnero, edad, telefono) y cuando se unen al club deben especificar cules son sus 3 obras literarias preferidas (se debe especificar subgnero narrativo y tipo) indicando el orden de preferencia. Un integrante puede mudarse de ciudad o pas y unirse a otro club o simplemente cambiar de club en su misma ciudad.  A medida que hagan vida en los clubes de lectura es importante tener para cada persona un historial que muestre por club antigedad y lista de libros analizadas en cada uno. Un miembro no puede estar activo en mas de un club a la vez y en cada uno pertenece a un solo grupo de lectura simultneamente.',
            reading_meetings: 'Reuniones',
            reading_mt1: ' Las reuniones semanales duran un mximo de 2 horas y si son reuniones de nios no deben terminar despus de las 7 pm. Por grupo de lectura, el objetivo de cada reunin es analizar un libro previamente acordado entre los lectores  la discusin puede durar de 1 a 3 reuniones mximo dependiendo del tamao y complejidad de la obra. Muchos clubes y grupos pueden estar analizando simultneamente el mismo libro. Cuando termine la discusin se debe registrar las conclusiones y la valoracin final del libro  un valor entre 1 y 5 acordado entre todos los lectores.  Si una misma obra es analizada por grupos distintos la valoracin registrada es el promedio de todos los grupos as que para un mismo club, consultas de esa lista pueden mostrar ordenamientos distintos ya que la valoracin de un libro puede cambiar en el tiempo.',
            reading_mt2: 'En cada reunin el moderador pasa asistencia a los presentes, si un integrante deja de asistir a mas del 30% de los encuentros en un bimestre es retirado del club y no se puede volver a unir al mismo. Cada club debe tener las fichas de los libros que han analizado a lo largo de su historia',
            reading_plays:'Obras actuadas',
            reading_p1: 'En estas actuaciones participan indistintamente los miembros del club y tambin pudieran participar integrantes de otros clubes asociados  usualmente la obra actuada se centra en la historia de algun libro previamente analizado (es como una obra de teatro). El costo de la entrada es fijada por cada club y puede variar de obra a obra. Algunas veces el mismo club tiene espacios fsicos que utiliza para la presentacin (auditorios o salas) o simplemente los alquilan. La capacidad de asistentes depende del auditorio o sala que tenga disponible el club. ',
            reading_p2: 'De cada obra actuada se debe saber a qu libro(s) hace referencia, tener un resumen de su argumento, el elenco (personajes y actores), calendario de presentaciones (que incluye fecha, lugar, hora inicio y duracin total). En cada presentacin realizada el pblico asistente vota por el mejor actor (pueden haber empates) y valora la obra con un mximo de 5 estrellas. Es parte de la informacin representativa de un club la lista de obras presentadas, su valoracin promedio (segn la valoracin individual obtenida en cada presentacin) y el ingreso total percibido hasta el momento de cada consulta realizada (hay obras que se pueden seguir presentando y otras ya tienen estatus de no activas).',
            reading_docs1: 'Diagramas de flujo de usuario de los procesos de negocio a automatizar, diseo de reportes y especificacin de requerimientos no funcionales',
            reading_docs2: 'Modelo entidad relacin y modelo relacional',
            reading_download2: 'Modelo relacional',
            reading_er: 'Modelo entidad relacin',
            reading_fi: 'Descargar imgen en tamao original',

