            /* TT Proto */
            tt_tit: 'Prototipo de sitio web ToyToys',
            tt_desc: 'ToyToys es una tienda ficticia de los Estados Unidos de Amrica fundada en la ciudad de Nueva York. Empez como una tienda que venda gran variedad de artculos, no obstante, luego de la Segunda Guerra mundial la venta de juguetes creci considerablemente, gracias a ese auge se le di, en 1953 el nombre de ToyToys.',
            tt_l1: 'Disear un prototipo de sitio web para la juguetera ToyToys centrado al usuario, (slo interfaz).',
