            tt_c2: 'Sistemas de pago',
            tt_c4: 'Usuario registrado',

            tt_docs: 'Documentacin',
            tt_docsinfo: 'A pesar de que el sitio web prototipado se centra slo en la interfaz de usuario la documentacin describe toda la funcionalidad y caractersticas del sitio web. Dicha documentacin se divide en los siguientes puntos: ',
            tt_di1: 'Personas',
