            osp_date: '23 de Junio de 2019',
            osp_details: 'Sobre el proyecto',
            osp_d1: 'La criptografa es el conjunto de estrategias que permiten transformar la informacin para ocultarla a terceros no autorizados. Los algoritmos ms utilizados se valen de tcnicas de substitucin y transposicin para modificar el texto original y obtener el texto cifrado. Un algoritmo antiguo y muy sencillo es el cdigo cesar que slo usa substitucin y consiste en lo siguiente: Cada letra del texto original se traduce por la letra que est dos posiciones ms adelante en el alfabeto. Las que estn al final del alfabeto Y y Z se substituyen por A y B respectivamente, es decir, que la estrategia de substitucin es circular. Por ejemplo cifrar el texto',
