            osp_d3: 'Otro algoritmo muy conocido es el cdigo murcilago que consiste en lo siguiente: Inicialmente la palabra murcilago tiene la caracterstica de poseer todas las vocales y tener una longitud de 10 caracteres. As se propone una substitucin letra a nmero, si la letra est en el texto en claro, bajo el siguiente patrn:',
            osp_t: 'Letra',
            osp_c: 'Conversin',
            osp_d4: 'This repository has a program written in C of the crypting/decrypting program, there is a version of 2 children processes and n children processes, the root will apply one crypting/decrypting method and each children will apply the other, the blank spaces will be deleted. The decyphering program is exactly the opposite, given the cyphered text in a text file, it will obtain the clear text without blank spaces or tabs. That means that the children processes will execute the reverser Murcielago code. The output files will be received by the superior level and will apply the caesar code in reverse.',
