            /* OS Fork and Thread */
            osft_tit: 'Programacin concurrente',
            osft_date: '19 de Junio de 2019',
            osft_d4: 'Este repositorio contiene tres versiones del programa de cifrado y descifrado de texto: una secuencial y dos versiones concurrentes: una usando procesos y otra usando hilos. Para las versiones concurrentes, se realiz con un rbol de procesos/hilos de tres niveles: raz, intermedios y hojas, de la siguiente manera: Para cifrar se recibe el texto en claro en un archivo de texto y se transforma usando los cdigos cesar y murcilago con los espacios en blanco eliminados. Los procesos/hilos hojas aplican cdigo cesar para la primera fase de cifrado guardando sus respuestas en archivos de texto. Los procesos/hilos intermedios recuperan el texto de los archivos creados por sus procesos/hilos hijos, los concatena en orden y aplica el cdigo murcilago para una segunda fase de substitucin. El proceso/hilo raz, recibe los textos cifrados de todos sus hijos del nivel intermedio, elimina los espacios en blancos o tabuladores y concatena ordenadamente los archivos de respuesta de cada proceso/hilo para finalmente obtener el texto cifrado y guardarlo en otro archivo de texto.',
            osft_d42: 'El programa de descifrado es justamente en reverso, es decir, dado el texto cifrado en un archivo de texto, se obtiene el texto en claro sin blancos ni tabuladores. Es decir, los procesos/hilos hojas ejecutan el cdigo murcilago en reverso. Los archivos de respuesta son recuperados por el nivel de procesos/hilos intermedio y aplican cdigo cesar en reverso. Finalmente los archivos creados son recuperados por el nivel ms alto que slo concatena adecuadamente los archivos en claro del nivel intermedio y as definitivamente obtener el archivo de texto en claro sin blancos ni tabuladores (stos fueron eliminados cuando el archivo de texto original se cifr). La lnea de comandos se realizar de la siguiente forma',
